@use "base";

.cover{
  @include base.flex-div--center(column, center, center);

  height: 100vh;
  color: base.$color-dark;
  font-family: base.$font-family-cover;

  &__title{
    margin: 3em;
    font-size: 3em;
  }

  &__discover{
    margin: 1em;
    font-size: 1.2em;
    text-align: center;

    &__letter{
      margin: 0 0.4em;
    }

    &__cursor{
      @include base.blinking(0s, 0.8s);
    }
  }

  &__scroll-down-icon{
    &--top{
      @include base.blinking(0s, 1.6s);
      margin-top: 2em;
    }

    &--bottom{
      @include base.blinking(0.2s, 1.6s);
      margin-top: 0.4em;
    }
  }
}


.section{
  &__title{
    padding-top: 2em;
    text-align: center;
    font-size: 3.2em;
    color: base.$color-dark;
    font-family: base.$font-family-title;
  }

  &__module{
    @include base.flex-div--center(column, center, normal);

    margin-top: 4em;
    margin-bottom: 1em;
    font-size: 1.2em;

    &__title{
      margin-bottom: 1em;
      font-size: 2em;
      color: base.$color-dark;
    }

    &__row{
      @include base.flex-div(row);
      position: relative;
      width: 80%;
      line-height: 1.6em;
      text-align: justify;
    }

    &__card{
      @include base.flex-div(column);
      flex: 1;

      margin: 2em;
      padding: 2em;

      font-size: 0.8em;
      font-weight: lighter;
      color: base.$color-card-text;
      background-color: base.$color-dark;

      @extend %clickable;

      &--center{
        align-items: center;
        text-align: center;
      }

      &__title{
        font-size: 1.2em;
      }

      &__title--bold{
        font-weight: bold;
      }

      &__br{
        display: block;
        width: 2.4em;
        height: 1px;
        margin: 1em 0;
        background-color: base.$color-light;
      }
    }
  }

  &__photo{
    height: 8em;
    width: 8em;
    border: 1px solid base.$color-dark;
    border-radius: 50%;
    margin-right: 2em;
    align-self: center;
  }
}

.contact{
  &__scroll-up-icon{
    @extend %clickable;

    margin-bottom: 1em;
    color: base.$color-dark;
  }


  &__btn-group{
    @include base.flex-div--center(row, center, center);
    width: 100%;
  }

  &__btn{
    @extend %clickable;

    margin: 2em;
    color: base.$color-dark;
  }
}